import * as React from 'react'
import {
  DialogContentText,
  DialogTitle,
  Icon,
  IconButton,
  Button,
  Checkbox,
  FormControlLabel,
  TextField,
  Dialog,
  DialogActions,
  DialogContent,
} from '@material-ui/core'
import { UrlStateContext } from '@src/Root/contexts'
import { UrlPathEnum } from '@src/types'
import { pushHistory } from '@src/history'

interface Prop {}

export const FilterDialog: React.FC<Prop> = (props) => {
  const urlFilters = React.useContext(UrlStateContext)
  const [open, setOpen] = React.useState(false)
  const [eventId, setEventId] = React.useState(urlFilters.eventId || '')
  const [isShared, setIsShared] = React.useState(urlFilters.isShared || false)
  const [searchText, setSearchText] = React.useState<string>(urlFilters.searchText || '')
  const [categoryId, setCategoryId] = React.useState<number | null>(urlFilters.categoryId || null)

  const resetFilters = () => {
    console.log('reset filters')
    setEventId(urlFilters.eventId || '')
    setIsShared(urlFilters.isShared || false)
    setSearchText(urlFilters.searchText || '')
    setCategoryId(urlFilters.categoryId || null)
  }

  const handleClickOpen = () => {
    resetFilters()
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  if (!open) {
    return (
      <IconButton onClick={handleClickOpen}>
        <Icon color="primary">filter_alt</Icon>
      </IconButton>
    )
  }
  return (
    <div>
      <IconButton onClick={handleClickOpen}>
        <Icon color="primary">filter_alt</Icon>
      </IconButton>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Filter Transactions</DialogTitle>
        {open ? (
          <DialogContent>
            <DialogContentText>
              Search transactions by category, text (merchant name and notes). By checking "Shared",
              you can limit the results to be shared transactions only.
            </DialogContentText>
            <TextField
              margin="dense"
              id="categoryId"
              label="Category"
              type="number"
              fullWidth
              value={categoryId || ''}
              onChange={(e) => setCategoryId(parseInt(e.target.value))}
            />
            <TextField
              margin="dense"
              id="eventId"
              label="Event"
              type="string"
              fullWidth
              value={eventId || ''}
              onChange={(e) => setEventId(e.target.value)}
            />
            <TextField
              onChange={(e) => setSearchText(e.target.value)}
              value={searchText || ''}
              margin="dense"
              id="searchText"
              label="Text Search"
              type="text"
              fullWidth
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={isShared}
                  onChange={(e) => setIsShared(e.target.checked)}
                  name="isShared"
                />
              }
              label="Shared"
            />
          </DialogContent>
        ) : null}
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button
            onClick={(e) => {
              pushHistory(UrlPathEnum.TransactionList, {
                eventId: eventId,
                searchText: searchText,
                isShared: isShared,
                categoryId: categoryId,
              })
              handleClose()
            }}
            color="primary"
          >
            Search
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
