import { Button, Grid, TextField } from '@material-ui/core'
import * as React from 'react'
import { isWide } from '@src/styles'
import { useApolloQuery } from '@src/queries/__generated__/ImportWaveTransactions.hooks'
import { useApolloQuery as GetLatestImportDate } from '@src/queries/__generated__/GetLatestImportDate.hooks'
import {
  GetTransactions,
  QUERY as GET_TRANSACTIONS_QUERY,
} from '@src/queries/__generated__/GetTransactions.hooks'

const today = new Date().toISOString().slice(0, 10)

const ImportButton: React.FC<{ onClose: () => void; startDate: string; endDate: string }> = ({
  onClose,
  startDate,
  endDate,
}) => {
  const [importWaveTransactions, { loading }] = useApolloQuery({
    update(cache, { data }) {
      if (data?.importFromWave.isOk) {
        const cached = cache.readQuery<GetTransactions>({ query: GET_TRANSACTIONS_QUERY })
        cache.writeQuery({
          query: GET_TRANSACTIONS_QUERY,
          data: Object.assign({}, cached, {
            transactions: [...data.importFromWave.transactions, ...cached!.transactions],
          }),
        })
        onClose()
      }
    },
  })

  return (
    <Button
      fullWidth={!isWide}
      onClick={() =>
        importWaveTransactions({
          variables: {
            startDate,
            endDate,
          },
        })
      }
      style={{ marginTop: '10px' }}
      color="primary"
      variant="contained"
    >
      {loading ? 'importing ...' : 'Import'}
    </Button>
  )
}

export const ImportForm = (props: { onClose: () => void }) => {
  const { data } = GetLatestImportDate({})
  if (!data) return <div></div>

  return <ImportFormComponent onClose={props.onClose} initialStartDate={data.latestImportDate} />
}

const ImportFormComponent = (props: { onClose: () => void; initialStartDate: string }) => {
  const [startDate, setStartDate] = React.useState(props.initialStartDate)
  const [endDate, setEndDate] = React.useState(today)

  return (
    <Grid container>
      <Grid item xs={12} sm={4}>
        <TextField
          fullWidth={!isWide}
          type="date"
          label="from"
          value={startDate}
          onChange={(e) => setStartDate(e.target.value)}
        />
      </Grid>
      <Grid item xs={12} sm={4}>
        <TextField
          fullWidth={!isWide}
          type="date"
          label="to"
          defaultValue={today}
          onChange={(e) => setEndDate(e.target.value)}
        />
      </Grid>
      <Grid item xs={12} sm={4}>
        <ImportButton onClose={props.onClose} startDate={startDate} endDate={endDate} />
      </Grid>
    </Grid>
  )
}
